    const lastNames = ["Иванов", "Петров", "Сидоров", "Кузнецов"];
    const firstNames = ["Иван", "Петр", "Александр", "Олег"];
    const middleNames = ["Иванович", "Петрович", "Александрович", "Олегович"];

    const randomPersons = Array.from(Array(20)).map(() => ({ name: getRandomName(), url: getRandomUrl() }));
    const randomPersonsWithImageAvatars = randomPersons.map((x) => ({ ...x, url: getRandomUrl(true) }));
    const randomPersonsWithoutImageAvatars = randomPersons.map((x) => ({ ...x, url: "" }));

    function getRandomUrl(alwaysWithImage: boolean = false) {
        const randomImageIdx = Math.floor(Math.random() * 70) + 1;
        const url = `https://i.pravatar.cc/300?img=${randomImageIdx}`;

        if (alwaysWithImage) return url;

        return Math.random() < 0.5 ? url : "";
    }

    function getRandomName() {
        const lastName = lastNames[Math.floor(Math.random() * lastNames.length)];
        const firstName = firstNames[Math.floor(Math.random() * firstNames.length)];
        const middleName = middleNames[Math.floor(Math.random() * middleNames.length)];

        return `${lastName} ${firstName} ${middleName}`;
    }
</script>

